<template>
  <v-container>
    <v-card class="mt-10 pa-4" elevation="12">
      <v-card-title>
        캐릭터 입력

        <v-col>
          <v-row>
            <v-col cols="3">
              <v-text-field
                ref="nameInput"
                v-model="name"
                label="캐릭터 이름"
                @keyup.enter="$refs.initiativeInput.focus()"
              ></v-text-field>
            </v-col>
            <v-col cols="3">
              <v-text-field
                ref="initiativeInput"
                v-model="initiative"
                label="우선권 보정치"
                @keyup.enter="$refs.acInput.focus()"
              ></v-text-field>
            </v-col>
            <v-col cols="3">
              <v-text-field
                ref="acInput"
                v-model="ac"
                label="AC"
                @keyup.enter="$refs.hpInput.focus()"
              ></v-text-field>
            </v-col>
            <v-col cols="3">
              <v-text-field
                ref="hpInput"
                v-model="hp"
                label="HP"
                @keyup.enter="enter"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-col>
      </v-card-title>

      <v-row>
        <v-col cols="12">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">캐릭터 이름</th>
                  <th class="text-left">AC</th>
                  <th class="text-left">우선권</th>
                  <th class="text-left">HP</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in players" :key="item.name">
                  <td>{{ item.name }}</td>
                  <td>{{ item.ac }}</td>
                  <td>{{ item.initiative }}</td>
                  <td>{{ item.hp }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-col>
      </v-row>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "PlayerStatusInsertCmp",
  data() {
    return {
      players: [],
      name: "",
      ac: "",
      hp: "",
      initiative: "",
    };
  },
  methods: {
    ...mapActions("player", ["savePlayers"]),

    enter() {
      const data = {
        type: "p", // player type
        name: this.name,
        ac: this.ac,
        hp: this.hp,
        hp_tmp: "",
        initiative: this.initiative,
        initiative_tmp: "",
        initiative_cal: "",
      };

      this.players.push(data);

      this.savePlayers(this.players);

      this.init_input();

      this.$refs.nameInput.focus(); // 입력 필드로 포커스 이동
    },
    init_input() {
      this.name = "";
      this.ac = "";
      this.hp = "";
      this.initiative = "";
    },
  },
  computed: {
    ...mapGetters("player", ["getPlayers"]),
  },
  created() {
    this.players = this.getPlayers;
  },
};
</script>
