<template>
  <v-app>
    <v-navigation-drawer
      app
      v-model="drawer"
      :clipped="clipped"
      :mini-variant.sync="mini"
    >
      <v-list>
        <v-list-item
          v-for="item in items"
          :key="item.title"
          @click="navigate(item.route)"
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>

    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "App",

  data: () => ({
    drawer: false,
    clipped: false,
    mini: false,
    items: [
      { title: "캐릭터", icon: "mdi-home", route: "/character" },
      { title: "몬스터", icon: "mdi-home", route: "/monster" },
      { title: "전투", icon: "mdi-information", route: "/battle" },
      { title: "실험실", icon: "mdi-information", route: "/test" },
      // Add more items as needed
    ],
  }),

  methods: {
    navigate(route) {
      if (this.$route.path !== route) {
        this.$router.push(route);
      }
      this.drawer = false; // Close the drawer after navigation
    },
  },
};
</script>
